/* eslint-disable vue/return-in-computed-property */
/* eslint-disable import/extensions */
<template>
  <a-layout-header :class="[theme, 'global-header']">
    <div :class="['global-header-wide', layout]">
      <router-link
        v-if="isMobile || layout === 'head'"
        to="/"
        :class="['logo', isMobile ? null : 'pc', theme]"
      >
        <img width="32" src="static/img/vue-antd-logo.png">
        <h1 v-if="!isMobile">{{systemName}}</h1>
      </router-link>
      <a-divider v-if="isMobile" type="vertical"/>
      <a-icon
        v-if="layout === 'side'"
        class="trigger"
        :type="collapsed ? 'menu-unfold' : 'menu-fold'"
        @click="toggleCollapse"
      />
      <div v-if="layout === 'head'" class="global-header-menu">
        <i-menu
          style="height: 64px; line-height: 64px;"
          :theme="theme"
          mode="horizontal"
          :menuData="menuData"
          @select="onSelect"
        />
      </div>
      <!-- 头部右边功能组 -->

      <div :class="['global-header-right', theme]">
        <span id="clickButton" @click="toggleCollapse"><a-icon type="menu-fold" /></span>
        <!-- 头部搜索框 -->
        <!-- <header-search class="header-item"/> -->
        <!-- 头部帮助 -->

        <!-- <a-tooltip class="header-item" title="帮助文档" placement="bottom">
          <a>
            <a-icon type="question-circle-o"/>
          </a>
        </a-tooltip> -->

        <!-- 头部信息提示 -->
        <!-- <header-notice class="header-item"/> -->
        <!-- 头部个人中心 -->
        <header-avatar class="header-item"/>
      </div>
    </div>
  </a-layout-header>
</template>

<script>
// eslint-disable-next-line import/extensions
// import HeaderNotice from './HeaderNotice'
// eslint-disable-next-line import/extensions
import HeaderAvatar from './HeaderlAvatar'

export default {
  name: 'GlobalHeader',
  data() {
    return {
      flag: false,
    }
  },
  // components: { IMenu, HeaderAvatar, HeaderNotice, HeaderSearch },
  components: { HeaderAvatar },
  props: ['collapsed', 'menuData'],
  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    isMobile() { },
    // eslint-disable-next-line vue/return-in-computed-property
    layout() {
      // return this.$store.state.setting.layout;
    },
    // eslint-disable-next-line vue/return-in-computed-property
    theme() {
      // return this.layout === "side" ? "light" : this.$store.state.setting.theme;
    },
    // eslint-disable-next-line vue/return-in-computed-property
    systemName() {
      // return this.$store.state.setting.systemName;
    },
  },
  methods: {
    toggleCollapse() {
      this.$emit('toggleCollapse')
      this.flag = !this.flag
      if (this.flag) {
        // console.log( document.getElementById('clickButton'))
        document.getElementById('clickButton').style.left = '120px'
      } else {
        document.getElementById('clickButton').style.left = '300px'
      }
    },
    onSelect(obj) {
      this.$emit('menuSelect', obj)
    },
  },
}
</script>

<style lang="less" scoped>
.trigger {
  font-size: 20px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
  &:hover {
    color: #1890ff;
  }
}
.header-item {
  padding: 0 12px;
  display: inline-block;
  height: 100%;
  cursor: pointer;
  vertical-align: middle;
  i {
    font-size: 16px;
    color: rgba(0, 0, 0, 0.65);
  }
}
.global-header {
  padding: 0 12px 0 0;
  -webkit-box-shadow: -2px 1px 4px rgba(0, 21, 41, 0.08);
  box-shadow: -2px 1px 4px rgba(0, 21, 41, 0.08);
  position: relative;
  &.light {
    background: #fff;
  }
  &.dark {
    background: #001529;
  }
  .global-header-wide {
    &.head {
      max-width: 1400px;
      margin: auto;
    }
    &.side {
    }
    .logo {
      height: 64px;
      line-height: 58px;
      vertical-align: top;
      display: inline-block;
      padding: 0 12px 0 24px;
      cursor: pointer;
      font-size: 20px;
      &.pc {
        padding: 0 12px 0 0;
      }
      img {
        display: inline-block;
        vertical-align: middle;
      }
      h1 {
        display: inline-block;
        font-size: 16px;
      }
      &.dark h1 {
        color: #fff;
      }
    }
    .global-header-menu {
      display: inline-block;
    }
    .global-header-right {
      float: right;
      &.dark {
        color: #fff;
        i {
          color: #fff;
        }
      }
    }
  }
}
#clickButton {
  position: absolute;
  left: 300px;
  top: 0px;
  font-size: 16px;
  cursor: pointer;
}
</style>
