<template>
  <div id="gs">
      <div class="s-header" >
        <img src="../assets/logo.png" alt="功倍CRM">
        <span v-if="!this.collapsed" class="title">{{ title }}</span>
      </div>
    <a-button v-if="false" type="primary" @click="toggleCollapsed" style="margin-bottom: 16px">
      <a-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'"/>
    </a-button>
    <a-menu
      mode="inline"
      theme="dark"
      :inlineCollapsed="collapsed">
      <!-- <a-menu-item key="1">
        <a-icon type="pie-chart"/>
        <span>首页</span>
      </a-menu-item> -->
      <a-menu-item key="1">
        <a-icon type="desktop"/>
        <span>客户管理</span>
        <router-link to='/home/customerlist'></router-link>
      </a-menu-item>
    </a-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      collapsed: false,
      title: '功倍CRM',
    }
  },

  methods: {
    toggleCollapsed() {
      this.collapsed = !this.collapsed
      // console.log(this.title)
      if (this.collapsed) {
        // console.log(document.getElementsByClassName('sider'))
        document.getElementById('gs').style.width = '80px'
        document.getElementsByClassName('sider')[0].style.width = '80px'
        document.getElementsByClassName('main')[0].style.paddingLeft = '80px'
        document.getElementsByClassName('ant-btn')[0].style.left = '90px'
        this.title = ''
      } else {
        document.getElementById('gs').style.width = '256px'
        document.getElementsByClassName('sider')[0].style.width = '256px'
        document.getElementsByClassName('main')[0].style.paddingLeft = '256px'
        document.getElementsByClassName('ant-btn')[0].style.left = '270px'
        this.title = '功倍CRM'
      }
    },
  },

  computed: {},
  created() {},
  mounted() {
    this.$router.push('/home/customerlist')
  },
}
</script>
<style lang='scss' scoped>
  .s-header {
    padding-left: 24px;
    height: 64px;
    line-height: 64px;
    font-size: 20px;
    cursor: pointer;
    background-color: #002140;
    img {
      width: 32px;
      margin-right: 12px;
    }
    .title{
      color: #fff;
    }
  }
</style>
